.icon-block {
  &--bordered {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    
    background-color: rgba(27, 27, 27, 0.71);
    box-shadow: 0 0 10px 1px #ffffff;
    
    min-height: 230px;
    
    padding: 20px 10px 30px;
  
    //*for animation*
    opacity: 0;
    
    @media (min-width: 769px ) {
      position: relative;
    }
    @media (max-width:480px ) {
      opacity: 1;
    }
  }

  &__img {
    width: 35px;
    height: 45px;
    margin-bottom: 20px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    text-align: center;
    h2{
      color: #ffffff;
      font-family: $fontSourceSansPro;
      font-size: 20px;
      font-weight: 400;
      line-height: 24px;
  
      margin-bottom: 20px;
  
      letter-spacing: 0.2px;
    }
    p {
      color: #ffffff;
      font-family: $fontSourceSansPro;
      font-size: 15px;
      font-weight: 300;
  
      letter-spacing: 0.15px;
    }
  }
}

